html,
body {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

a {
    text-decoration: none;
}

#root {
    width: 100%;
    height: 100%;
}
.App {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;
    background-image: linear-gradient(
        to right,
        #444 0%,
        gray 5%,
        white 65%,
        #777 85%,
        #444 99%
    );
}

.App__content {
    width: 80%;
    height: 90%;
    border-top: 10px solid green;
    border-left: 10px solid green;
    border-radius: 40px;
    background-color: rgb(133, 210, 154);
    overflow: hidden;
}
.welcome {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    width: 100%;
    height: 100%;
}

.welcome__logo span {
    font-family: Arial, sans-serif;
    font-size: 170px;
    font-weight: bold;
    text-transform: uppercase;
}

.welcome__button {
    display: inline-block;
    padding: 30px 60px;
    font-size: 64px;
    text-transform: uppercase;
    color: #ffffff;
    background-color: #000000;
    border-radius: 50%;
}
.Mode {
    height: 100%;
}

.Mode__list {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;
    padding-left: 0;
    margin-top: 0;
    margin-bottom: 0;
    list-style: none;
}

.Mode__link {
    display: inline-block;
    padding: 30px 60px;
    font-size: 48px;
    text-transform: uppercase;
    color: #ffffff;
    background-color: #000000;
    border-radius: 50%;
}
.Game {
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;
}

.Game__svg {
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 10px;
}

.Game__overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: rgba(0, 0, 0, 0.4);
}

.Game__overlay:hover {
    cursor: pointer;
}

.Game__overlay-text {
    margin-top: 0;
    margin-bottom: 0;
    font-size: 48px;
    color: #ffffff;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.Game__overlay_hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    border: 0;  
    overflow: hidden;
    clip: rect(0 0 0 0);
}

