.App {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    background-image: linear-gradient(
        to right,
        #444 0%,
        gray 5%,
        white 65%,
        #777 85%,
        #444 99%
    );
}

.App__content {
    width: 80%;
    height: 90%;
    border-top: 10px solid green;
    border-left: 10px solid green;
    border-radius: 40px;
    background-color: rgb(133, 210, 154);
    overflow: hidden;
}